<template>
	<view class="main">
		<view class="hand">
			<image src="../../static/tabBarimg/dbleft.png" @click="blackbtn"></image>
			设备统计
		</view>
		<view class="show">
			<view class="sone">
				<view class="sonetap">
					<view class="stapleft">
						<view class="staplefts">{{list.projectTotal}}</view>
						<view class="stapleftx">项目总数(个)</view>
					</view>
					<view class="stapleft">
						<view class="staplefts">{{list.hostTotal}}</view>
						<view class="stapleftx">从机个数(个)</view>
					</view>
				</view>
			</view>
			<view class="stwo">
				<view class="stwotap" v-for="(item, index) in list.list" :key="index">
					<view class="stwoleft">
						<view class="stwolefts">{{item.projectName}}</view>
					</view>
					<view class="stwoleftx">从机个数{{item.equCount}}个</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	
	export default {
		components: {},
		mounted() {
			
		},

		data() {
			return {
				list:{},//列表数据
			}
		},
		onLoad() {
			this.show()
		},
		methods: {
			//设备统计
			show(){
				uni.showLoading({
					title: '加载中...'
				})
				this.$base.request1('manager/statistic/equipment', 'GET')
					.then(res => {
						uni.hideLoading()
						this.list=res.data.data
					})
					.catch(err => {
				
					})
			},
			//返回上一页
			blackbtn() {
				uni.navigateBack()
			},
			
		}
	}
</script>

